import { getType } from 'typesafe-actions';
import { fetchSourcesFailure, fetchSourcesRequest, fetchSourcesSuccess, } from './actions';
export const stateKey = 'sources';
export const defaultState = {
    sources: null,
    error: null,
    status: 0 /* none */,
};
export const reducer = (state = defaultState, action) => {
    switch (action.type) {
        case getType(fetchSourcesRequest):
            return Object.assign({}, state, { status: 1 /* inProgress */ });
        case getType(fetchSourcesSuccess):
            return Object.assign({}, state, { status: 2 /* complete */, error: null, sources: action.payload.data });
        case getType(fetchSourcesFailure):
            return Object.assign({}, state, { status: 2 /* complete */, error: action.payload });
        default:
            return state;
    }
};
